import MtaH5 from 'mta-h5-analysis';
import router from '@/router';
import Vue    from 'vue';

const VUE_APP_MTA_SID = process.env.VUE_APP_MTA_SID;
const VUE_APP_MTA_CID = process.env.VUE_APP_MTA_CID;

const vm = new Vue();

export default {
  init() {
    MtaH5.init({
      "sid": VUE_APP_MTA_SID, //appid
      "cid": VUE_APP_MTA_CID, //
      "autoReport": 1,//(1:init,0:pgv)
      "senseHash": 1, //hashurl
      "senseQuery": 1, //urlurl
      "performanceMonitor": 1,//
      "ignoreParams": [] //url
    });

    vm.$watch(() => router.app.$route, () => {
      MtaH5.pgv();
    });
  }
}
