import api from '@/api';

export default {
  // recommendedCommodity , ecProduct , talkTopic , campaign 
  //  type: Booking,Buying,Saying,Campaigning
  // Key ID

  recordCampaignPopularStatistics: function (campaigningId) {
    api.popularStatistics.recordPopularStatistics({ type: 'Campaigning', key: campaigningId });
  },
  recordRecommendedCommodityPopularStatistics: function (recommendedCommodityId) {
    api.popularStatistics.recordPopularStatistics({ type: 'Buying', key: recommendedCommodityId });
  },
  recordEcProductPopularStatistics: function (ecProductId) {
    api.popularStatistics.recordPopularStatistics({ type: 'Booking', key: ecProductId });
  },
  recordTalkTopicPopularStatistics: function (talkTopicId) {
    api.popularStatistics.recordPopularStatistics({ type: 'Saying', key: talkTopicId });
  }
};