const REGIONS = [
  {
      regionBlockCode: 'sh-lawson',
      regionName: ''
  },
  {
    regionBlockCode: 'bj-lawson',
    regionName: ''
  },
  {
    regionBlockCode: 'dl-lawson',
    regionName: ''
  },
  {
    regionBlockCode: 'wh-lawson',
    regionName: ''
  },
  {
    regionBlockCode: 'cq-lawson',
    regionName: ''
  },
  {
    regionBlockCode: 'ah-lawson',
    regionName: ''
  }
];

const DEFAULT_REGION = {
  regionBlockCode: 'sh-lawson',
  regionName: ''
};

export default {
  getCurrentRegionName(regionBlockCode) {
    return REGIONS.find(r => r.regionBlockCode === regionBlockCode)['regionName'];
  }
}
