export default {
  getType(coupon) {
    if (coupon.type === 1) {
      return 'productPriceMinusToFreeCoupon'; // 
    } else if (coupon.type === 2) {
      return 'totalPriceMinusSomeCoupon'; // 
    } else if (coupon.type === 3) {
      return 'productPriceMinusSomeCoupon'; // 
    } else if (coupon.type === 0) {
      return 'productPriceMinusToSomeCoupon'; // 
    }
  }
};