<template>
  <div class="location-picker">
    <!--  -->
    <iframe width="100%" height="100%" frameborder=0 src="https://apis.map.qq.com/tools/locpicker?type=1&policy=1&key=QVPBZ-YBMHV-KYMPX-UIXW2-7AM7Z-BNFNU&referer=locator" ref="locationPicker"></iframe>
  </div>
</template>

<script>
export default {
  data() {
    return {
      chosenAddressName: null
    }
  },
  created() {
    this.initQQMapLocationPicker();
  },
  methods: {
    initQQMapLocationPicker() {
      window.addEventListener('message', this.handleLocationChange, false);
    },
    handleLocationChange(e) {
      // 
      const location = e.data;
      if (location && location.module == 'locationPicker') {//postmodule'locationPicker'
        // HACK poiname''poi
        const isPoinameInvalid = location.poiname === '' ? true : false;
        if (isPoinameInvalid) {
          qqLbsService.getAddressByLatLng(location.latlng.lat, location.latlng.lng).then(addressInfo => {
            const nearestPoi = addressInfo.nearPois[0];
            this.chosenAddressName = nearestPoi;
            this.hideLocationPicker();
          });
        } else {
          this.chosenAddressName = location.poiname;
          this.hideLocationPicker();
        }

        const editAddress = this.$store.state.campaignExchangeAddress.editAddressInfo;

        const name              = editAddress.name;
        const gender            = editAddress.gender;
        const phoneNumber       = editAddress.phoneNumber;
        const addressName       = this.chosenAddressName;
        const addressDetailName = location.poiaddress;
        const addressExtraInfo  = editAddress.addressExtraInfo;
        const addressId         = editAddress.addressId;
        const tags              = editAddress.tags;

        const editAddressInfoForChosen = {
          name,
          gender,
          phoneNumber,
          addressName,
          addressDetailName,
          addressExtraInfo,
          addressId,
          tags,
          latitude: location.latlng.lat,
          longitude: location.latlng.lng
        }

        this.$store.commit('campaignExchangeAddress/setEditAddressInfo', editAddressInfoForChosen);

        this.$router.go(-1);
      }
    },

    hideLocationPicker() {
      // HACK iOS Webviewiframeinputiframeinput
      const $locationPicker = this.$refs.locationPicker;
      $locationPicker.focus();
      $locationPicker.blur();
      window.removeEventListener('message', this.handleLocationChange, false);
    },
  }
}
</script>

<style lang="scss" scoped>
.location-picker {
  position: absolute;
  top: 0;
  width: 100vw;
  height: 100vh;
  background-color: white;
}
</style>
