export default {
  getType(coupon) {
      if(coupon.couponType === 1) {
        return 'couponcouponType:freeOnItem'; // 
      }
      else if (coupon.couponType === 2) {
        return 'couponcouponType:discountOnReachingTotal'; // 
      }
      else if (coupon.couponType === 3) {
        return 'couponType:discountOnItem'; // 
      }
  },
};
